/* ----------------------------------------
   DESIGN SYSTEM VARIABLES / VARIABLES
   ---------------------------------------- */

   :root {
    /* Brand Colors */
    --color-brand-darkblue: #1e2b79;
    --color-brand-blue: #2b3fb8;
    --color-brand-sky: #6fdbfc;
    --color-brand-cream: #f3f2ec;
    --color-brand-red: #ff6a6a;
    --color-brand-orange: #fea91d;
    --color-brand-yellow: #fcd41d;
    --color-brand-lime: #c0e333;
    --color-brand-purple: #c181fd;
    --color-brand-pink: #ff8ecc;
    --color-white: #ffffff;
  
    /* Grid System - Mobile */
    --grid-columns-mobile: 4;
    --grid-margin-mobile: 24px;
    --grid-gutter-mobile: 20px;
  
    /* Grid System - Desktop */
    --grid-columns-desktop: 12;
    --grid-margin-desktop: 90px;
    --grid-gutter-desktop: 32px;
  
    /* Section Spacing */
    --section-spacing-mobile: 60px;
    --section-spacing-desktop: 130px;
  
    /* Title/Content Spacing */
    --title-content-spacing-mobile: 20px;
    --title-content-spacing-desktop: 30px;
  
    /* Calculate scrollbar width for layout shifts */
    --scrollbar-width: 0px;
  }